import React from "react";
import Home from "./pages/Home";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

export default function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/clock">
            <Clock />
          </Route>
          <Route path="/page2">
            <Page2 />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

function Clock() {
  return (
    <div>
      <h2>Page1</h2>
    </div>
  );
}

function Page2() {
  return (
    <div>
      <h2>Page2</h2>
    </div>
  );
}
